'use client'

import { useState } from 'react'
import Link from 'next/link'
import { SmartLink, SmartLinkClick } from '@/lib/conversations/types'
import { mockSmartLinks, mockSmartLinkClicks } from '@/lib/conversations/mock-data'

// ==========================================
// RISIVO CRM - Smart Links Page
// Trackable links for emails/messages with analytics
// ==========================================

// Tab Configuration - Shared across Conversations module
const CONVERSATION_TABS = [
  { id: 'conversations', label: 'Conversations', href: '/sub-account/conversations' },
  { id: 'power-dialer', label: 'Power Dialer', href: '/sub-account/conversations/power-dialer' },
  { id: 'manual-actions', label: 'Manual Actions', href: '/sub-account/conversations/manual-actions' },
  { id: 'message-templates', label: 'Message Templates', href: '/sub-account/conversations/message-templates' },
  { id: 'smart-links', label: 'Smart Links', href: '/sub-account/conversations/smart-links' },
]

export default function SmartLinksPage() {
  const [links, setLinks] = useState<SmartLink[]>(mockSmartLinks)
  const [selectedLinks, setSelectedLinks] = useState<string[]>([])
  const [searchQuery, setSearchQuery] = useState('')
  const [showNewLinkModal, setShowNewLinkModal] = useState(false)
  const [showAnalyticsModal, setShowAnalyticsModal] = useState(false)
  const [selectedLinkForAnalytics, setSelectedLinkForAnalytics] = useState<SmartLink | null>(null)
  const [copiedLinkId, setCopiedLinkId] = useState<string | null>(null)

  // New link form state
  const [newLink, setNewLink] = useState({
    name: '',
    url: '',
    triggersAutomation: false,
    automationId: '',
  })

  // Filter links
  const filteredLinks = links.filter(link => {
    if (searchQuery) {
      const query = searchQuery.toLowerCase()
      return link.name.toLowerCase().includes(query) || 
             link.url.toLowerCase().includes(query) ||
             link.linkKey.toLowerCase().includes(query)
    }
    return true
  })

  // Format date
  const formatDate = (dateString: string) => {
    return new Date(dateString).toLocaleDateString('en-US', {
      day: 'numeric',
      month: 'short',
      year: 'numeric',
      hour: 'numeric',
      minute: '2-digit',
      hour12: true,
    })
  }

  // Handle select all
  const handleSelectAll = () => {
    if (selectedLinks.length === filteredLinks.length) {
      setSelectedLinks([])
    } else {
      setSelectedLinks(filteredLinks.map(l => l.id))
    }
  }

  // Generate unique link key
  const generateLinkKey = () => {
    const chars = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789'
    let result = ''
    for (let i = 0; i < 21; i++) {
      result += chars.charAt(Math.floor(Math.random() * chars.length))
    }
    return result
  }

  // Handle create link
  const handleCreateLink = () => {
    const link: SmartLink = {
      id: `link-${Date.now()}`,
      name: newLink.name,
      url: newLink.url,
      linkKey: generateLinkKey(),
      clicks: 0,
      uniqueClicks: 0,
      triggersAutomation: newLink.triggersAutomation,
      automationId: newLink.automationId || undefined,
      createdBy: 'user-001',
      createdByName: 'Jean Pierre',
      createdAt: new Date().toISOString(),
      updatedAt: new Date().toISOString(),
    }
    setLinks([link, ...links])
    setShowNewLinkModal(false)
    setNewLink({ name: '', url: '', triggersAutomation: false, automationId: '' })
  }

  // Handle delete link
  const handleDeleteLink = (linkId: string) => {
    setLinks(links.filter(l => l.id !== linkId))
  }

  // Copy tracking URL to clipboard
  const copyTrackingUrl = (link: SmartLink) => {
    // Generate tracking URL (in production, this would be your CRM's tracking domain)
    const trackingUrl = `https://track.risivo.com/l/${link.linkKey}`
    navigator.clipboard.writeText(trackingUrl)
    setCopiedLinkId(link.id)
    setTimeout(() => setCopiedLinkId(null), 2000)
  }

  // Open analytics modal
  const openAnalytics = (link: SmartLink) => {
    setSelectedLinkForAnalytics(link)
    setShowAnalyticsModal(true)
  }

  // Get clicks for a specific link
  const getLinkClicks = (linkId: string) => {
    return mockSmartLinkClicks.filter(click => click.linkId === linkId)
  }

  return (
    <div className="h-[calc(100vh-8rem)] flex flex-col">
      {/* Page Header with Tabs */}
      <div className="flex-none bg-white border-b border-gray-200 px-6 py-4">
        <div className="flex items-center justify-between mb-4">
          <h1 className="text-2xl font-bold text-gray-900">Conversations</h1>
        </div>
        
        {/* Tab Navigation */}
        <div className="flex items-center gap-6 border-b border-gray-200 -mb-4">
          {CONVERSATION_TABS.map(tab => (
            <Link
              key={tab.id}
              href={tab.href}
              className={`pb-3 px-1 text-sm font-medium border-b-2 transition-colors ${
                tab.id === 'smart-links'
                  ? 'border-blue-600 text-blue-600'
                  : 'border-transparent text-gray-500 hover:text-gray-700'
              }`}
            >
              {tab.label}
            </Link>
          ))}
        </div>
      </div>

      {/* Main Content */}
      <div className="flex-1 overflow-auto bg-gray-50 p-6">
        <div className="bg-white rounded-lg shadow-sm border border-gray-200">
          {/* Header */}
          <div className="p-6 border-b border-gray-200">
            <div className="flex items-center justify-between mb-4">
              <div>
                <h2 className="text-xl font-bold text-gray-900">Smart Links</h2>
                <p className="text-sm text-gray-500 mt-1">
                  Create trackable links for your emails and messages. Monitor click activity and trigger automations when links are clicked.
                </p>
              </div>
              <button
                onClick={() => setShowNewLinkModal(true)}
                className="px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 font-medium flex items-center gap-2"
              >
                <svg className="w-5 h-5" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 4v16m8-8H4" />
                </svg>
                Add Trigger Link
              </button>
            </div>

            {/* Search */}
            <div className="flex items-center justify-between">
              <div className="relative w-64">
                <svg className="absolute left-3 top-1/2 -translate-y-1/2 w-4 h-4 text-gray-400" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z" />
                </svg>
                <input
                  type="text"
                  placeholder="Search"
                  value={searchQuery}
                  onChange={(e) => setSearchQuery(e.target.value)}
                  className="w-full pl-10 pr-4 py-2 border border-gray-300 rounded-lg text-sm focus:outline-none focus:ring-2 focus:ring-blue-500"
                />
              </div>
              <div className="text-sm text-gray-500">
                {filteredLinks.length} link{filteredLinks.length !== 1 ? 's' : ''}
              </div>
            </div>
          </div>

          {/* Table */}
          <div className="overflow-x-auto">
            <table className="w-full">
              <thead className="bg-gray-50 border-b border-gray-200">
                <tr>
                  <th className="px-6 py-3 text-left">
                    <input
                      type="checkbox"
                      checked={selectedLinks.length === filteredLinks.length && filteredLinks.length > 0}
                      onChange={handleSelectAll}
                      className="rounded border-gray-300"
                    />
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Name
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Redirect URL
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Link Key
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Clicks
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Unique Clicks
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Automation
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Created
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Actions
                  </th>
                </tr>
              </thead>
              <tbody className="divide-y divide-gray-200">
                {filteredLinks.length === 0 ? (
                  <tr>
                    <td colSpan={9} className="px-6 py-16 text-center">
                      <div className="flex flex-col items-center">
                        <svg className="w-12 h-12 text-gray-400 mb-4" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={1.5} d="M13.828 10.172a4 4 0 00-5.656 0l-4 4a4 4 0 105.656 5.656l1.102-1.101m-.758-4.899a4 4 0 005.656 0l4-4a4 4 0 00-5.656-5.656l-1.1 1.1" />
                        </svg>
                        <h3 className="text-lg font-semibold text-gray-900 mb-1">No smart links found</h3>
                        <p className="text-gray-500">Create your first trackable link to get started</p>
                      </div>
                    </td>
                  </tr>
                ) : (
                  filteredLinks.map(link => (
                    <tr key={link.id} className="hover:bg-gray-50">
                      <td className="px-6 py-4">
                        <input
                          type="checkbox"
                          checked={selectedLinks.includes(link.id)}
                          onChange={(e) => {
                            if (e.target.checked) {
                              setSelectedLinks([...selectedLinks, link.id])
                            } else {
                              setSelectedLinks(selectedLinks.filter(id => id !== link.id))
                            }
                          }}
                          className="rounded border-gray-300"
                        />
                      </td>
                      <td className="px-6 py-4">
                        <p className="font-medium text-gray-900 truncate max-w-[200px]">
                          {link.name}
                        </p>
                      </td>
                      <td className="px-6 py-4">
                        <a 
                          href={link.url} 
                          target="_blank" 
                          rel="noopener noreferrer"
                          className="text-sm text-blue-600 hover:underline truncate block max-w-[250px]"
                        >
                          {link.url}
                        </a>
                      </td>
                      <td className="px-6 py-4">
                        <code className="text-xs bg-gray-100 px-2 py-1 rounded font-mono text-gray-700">
                          {link.linkKey}
                        </code>
                      </td>
                      <td className="px-6 py-4">
                        <button
                          onClick={() => openAnalytics(link)}
                          className="text-blue-600 hover:underline font-medium"
                        >
                          {link.clicks.toLocaleString()}
                        </button>
                      </td>
                      <td className="px-6 py-4 text-sm text-gray-600">
                        {link.uniqueClicks.toLocaleString()}
                      </td>
                      <td className="px-6 py-4">
                        {link.triggersAutomation ? (
                          <span className="inline-flex items-center gap-1 text-xs bg-green-100 text-green-700 px-2 py-1 rounded-full">
                            <svg className="w-3 h-3" fill="currentColor" viewBox="0 0 20 20">
                              <path fillRule="evenodd" d="M11.3 1.046A1 1 0 0112 2v5h4a1 1 0 01.82 1.573l-7 10A1 1 0 018 18v-5H4a1 1 0 01-.82-1.573l7-10a1 1 0 011.12-.38z" clipRule="evenodd" />
                            </svg>
                            {link.automationName || 'Enabled'}
                          </span>
                        ) : (
                          <span className="text-xs text-gray-400">No automation</span>
                        )}
                      </td>
                      <td className="px-6 py-4 text-sm text-gray-500">
                        {formatDate(link.createdAt)}
                      </td>
                      <td className="px-6 py-4">
                        <div className="flex items-center gap-1">
                          <button
                            onClick={() => copyTrackingUrl(link)}
                            className={`p-1.5 rounded transition-colors ${
                              copiedLinkId === link.id 
                                ? 'text-green-600 bg-green-50' 
                                : 'text-gray-400 hover:text-gray-600 hover:bg-gray-100'
                            }`}
                            title={copiedLinkId === link.id ? 'Copied!' : 'Copy tracking URL'}
                          >
                            {copiedLinkId === link.id ? (
                              <svg className="w-4 h-4" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M5 13l4 4L19 7" />
                              </svg>
                            ) : (
                              <svg className="w-4 h-4" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M8 16H6a2 2 0 01-2-2V6a2 2 0 012-2h8a2 2 0 012 2v2m-6 12h8a2 2 0 002-2v-8a2 2 0 00-2-2h-8a2 2 0 00-2 2v8a2 2 0 002 2z" />
                              </svg>
                            )}
                          </button>
                          <button
                            onClick={() => openAnalytics(link)}
                            className="p-1.5 text-gray-400 hover:text-gray-600 hover:bg-gray-100 rounded"
                            title="View analytics"
                          >
                            <svg className="w-4 h-4" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 19v-6a2 2 0 00-2-2H5a2 2 0 00-2 2v6a2 2 0 002 2h2a2 2 0 002-2zm0 0V9a2 2 0 012-2h2a2 2 0 012 2v10m-6 0a2 2 0 002 2h2a2 2 0 002-2m0 0V5a2 2 0 012-2h2a2 2 0 012 2v14a2 2 0 01-2 2h-2a2 2 0 01-2-2z" />
                            </svg>
                          </button>
                          <button
                            onClick={() => handleDeleteLink(link.id)}
                            className="p-1.5 text-gray-400 hover:text-red-600 hover:bg-red-50 rounded"
                            title="Delete"
                          >
                            <svg className="w-4 h-4" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 7l-.867 12.142A2 2 0 0116.138 21H7.862a2 2 0 01-1.995-1.858L5 7m5 4v6m4-6v6m1-10V4a1 1 0 00-1-1h-4a1 1 0 00-1 1v3M4 7h16" />
                            </svg>
                          </button>
                        </div>
                      </td>
                    </tr>
                  ))
                )}
              </tbody>
            </table>
          </div>

          {/* Pagination */}
          {filteredLinks.length > 0 && (
            <div className="px-6 py-4 border-t border-gray-200 flex items-center justify-between">
              <p className="text-sm text-gray-500">
                Showing {filteredLinks.length} of {links.length} links
              </p>
              <div className="flex items-center gap-2">
                <span className="text-sm text-gray-500">Rows per page:</span>
                <select className="border border-gray-300 rounded px-2 py-1 text-sm">
                  <option>20</option>
                  <option>50</option>
                  <option>100</option>
                </select>
              </div>
            </div>
          )}
        </div>
      </div>

      {/* New Link Modal */}
      {showNewLinkModal && (
        <div className="fixed inset-0 bg-black/50 flex items-center justify-center z-50">
          <div className="bg-white rounded-xl shadow-2xl w-full max-w-lg">
            <div className="flex items-center justify-between p-4 border-b border-gray-200">
              <h2 className="text-lg font-semibold text-gray-900">Add Trigger Link</h2>
              <button onClick={() => setShowNewLinkModal(false)} className="text-gray-400 hover:text-gray-600">
                <svg className="w-6 h-6" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                </svg>
              </button>
            </div>
            <div className="p-4 space-y-4">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">Link Name</label>
                <input
                  type="text"
                  value={newLink.name}
                  onChange={(e) => setNewLink({ ...newLink, name: e.target.value })}
                  placeholder="e.g., Book a Demo"
                  className="w-full border border-gray-300 rounded-lg px-3 py-2"
                />
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">Redirect URL</label>
                <input
                  type="url"
                  value={newLink.url}
                  onChange={(e) => setNewLink({ ...newLink, url: e.target.value })}
                  placeholder="https://example.com/landing-page"
                  className="w-full border border-gray-300 rounded-lg px-3 py-2"
                />
                <p className="text-xs text-gray-500 mt-1">
                  The URL where users will be redirected after clicking the tracking link
                </p>
              </div>
              <div className="bg-gray-50 rounded-lg p-4">
                <div className="flex items-center justify-between">
                  <div>
                    <h4 className="font-medium text-gray-900">Trigger Automation</h4>
                    <p className="text-sm text-gray-500">Run an automation when this link is clicked</p>
                  </div>
                  <label className="relative inline-flex items-center cursor-pointer">
                    <input
                      type="checkbox"
                      checked={newLink.triggersAutomation}
                      onChange={(e) => setNewLink({ ...newLink, triggersAutomation: e.target.checked })}
                      className="sr-only peer"
                    />
                    <div className="w-11 h-6 bg-gray-200 peer-focus:outline-none peer-focus:ring-4 peer-focus:ring-blue-300 rounded-full peer peer-checked:after:translate-x-full peer-checked:after:border-white after:content-[''] after:absolute after:top-[2px] after:left-[2px] after:bg-white after:border-gray-300 after:border after:rounded-full after:h-5 after:w-5 after:transition-all peer-checked:bg-blue-600"></div>
                  </label>
                </div>
                {newLink.triggersAutomation && (
                  <div className="mt-4">
                    <label className="block text-sm font-medium text-gray-700 mb-1">Select Automation</label>
                    <select
                      value={newLink.automationId}
                      onChange={(e) => setNewLink({ ...newLink, automationId: e.target.value })}
                      className="w-full border border-gray-300 rounded-lg px-3 py-2"
                    >
                      <option value="">Select an automation...</option>
                      <option value="auto-001">Booking Follow-up</option>
                      <option value="auto-002">LP Visit Nurture</option>
                      <option value="auto-003">FR Booking Sequence</option>
                    </select>
                  </div>
                )}
              </div>
            </div>
            <div className="flex justify-end gap-2 p-4 border-t border-gray-200">
              <button
                onClick={() => setShowNewLinkModal(false)}
                className="px-4 py-2 border border-gray-300 text-gray-700 rounded-lg hover:bg-gray-50"
              >
                Cancel
              </button>
              <button
                onClick={handleCreateLink}
                disabled={!newLink.name || !newLink.url}
                className="px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 disabled:opacity-50"
              >
                Create Link
              </button>
            </div>
          </div>
        </div>
      )}

      {/* Analytics Modal */}
      {showAnalyticsModal && selectedLinkForAnalytics && (
        <div className="fixed inset-0 bg-black/50 flex items-center justify-center z-50">
          <div className="bg-white rounded-xl shadow-2xl w-full max-w-3xl max-h-[80vh] overflow-hidden">
            <div className="flex items-center justify-between p-4 border-b border-gray-200">
              <div>
                <h2 className="text-lg font-semibold text-gray-900">Link Analytics</h2>
                <p className="text-sm text-gray-500">{selectedLinkForAnalytics.name}</p>
              </div>
              <button onClick={() => setShowAnalyticsModal(false)} className="text-gray-400 hover:text-gray-600">
                <svg className="w-6 h-6" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                </svg>
              </button>
            </div>
            
            {/* Stats Summary */}
            <div className="grid grid-cols-3 gap-4 p-4 bg-gray-50 border-b border-gray-200">
              <div className="text-center">
                <p className="text-3xl font-bold text-gray-900">{selectedLinkForAnalytics.clicks}</p>
                <p className="text-sm text-gray-500">Total Clicks</p>
              </div>
              <div className="text-center">
                <p className="text-3xl font-bold text-gray-900">{selectedLinkForAnalytics.uniqueClicks}</p>
                <p className="text-sm text-gray-500">Unique Clicks</p>
              </div>
              <div className="text-center">
                <p className="text-3xl font-bold text-gray-900">
                  {selectedLinkForAnalytics.clicks > 0 
                    ? Math.round((selectedLinkForAnalytics.uniqueClicks / selectedLinkForAnalytics.clicks) * 100)
                    : 0}%
                </p>
                <p className="text-sm text-gray-500">Unique Rate</p>
              </div>
            </div>

            {/* Click History */}
            <div className="p-4 overflow-y-auto max-h-[400px]">
              <h3 className="font-medium text-gray-900 mb-3">Recent Clicks</h3>
              {getLinkClicks(selectedLinkForAnalytics.id).length === 0 ? (
                <div className="text-center py-8 text-gray-500">
                  <p>No click data available</p>
                </div>
              ) : (
                <table className="w-full">
                  <thead className="bg-gray-50">
                    <tr>
                      <th className="px-4 py-2 text-left text-xs font-medium text-gray-500 uppercase">Contact</th>
                      <th className="px-4 py-2 text-left text-xs font-medium text-gray-500 uppercase">Email</th>
                      <th className="px-4 py-2 text-left text-xs font-medium text-gray-500 uppercase">IP Address</th>
                      <th className="px-4 py-2 text-left text-xs font-medium text-gray-500 uppercase">Clicked At</th>
                    </tr>
                  </thead>
                  <tbody className="divide-y divide-gray-200">
                    {getLinkClicks(selectedLinkForAnalytics.id).map(click => (
                      <tr key={click.id}>
                        <td className="px-4 py-3 text-sm font-medium text-gray-900">
                          {click.contactName || 'Unknown'}
                        </td>
                        <td className="px-4 py-3 text-sm text-gray-600">
                          {click.contactEmail || '-'}
                        </td>
                        <td className="px-4 py-3 text-sm text-gray-500 font-mono">
                          {click.ipAddress || '-'}
                        </td>
                        <td className="px-4 py-3 text-sm text-gray-500">
                          {formatDate(click.clickedAt)}
                        </td>
                      </tr>
                    ))}
                  </tbody>
                </table>
              )}
            </div>

            <div className="flex justify-end gap-2 p-4 border-t border-gray-200">
              <button
                onClick={() => setShowAnalyticsModal(false)}
                className="px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700"
              >
                Close
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  )
}
